@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Roboto:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-family: 'Montserrat', sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.failed {
    color: red;
    display: flex;
    justify-content: center;
}

.success {
    color: green;
    display: flex;
    justify-content: center;
}

.loader {
    display: flex;
    flex-direction: column;
    justify-content: center;    
    align-items: center;    
    height: 100vh;     
}

.heading-container {
    display: flex;
    flex-direction: column;    
    padding-top: 10rem;
    align-items: center; 
    text-align: center; 
}

.heading {
    font-size: 4rem;    
}

.website {
    font-size: 1.5rem;
}

.background {    
    position: absolute; 
    z-index: 0;    
    background-image: url(/slack-app/static/media/bg.8c5acad9.svg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100vh
}

@media screen and (max-width: 1600px) {
    .heading-container {
    padding-top: 5rem
    }
    .background {
    background-image: url(/slack-app/static/media/bg1600.d1ebc2ec.svg); 
    }
}

@media screen and (max-width: 1300px) {
    .background {
        background-image: url(/slack-app/static/media/bg768.e6902437.svg); 
    }    
}

@media screen and (max-width: 1400px) {
    .heading-container {
        padding-top: 1rem
    }
}

@media screen and (max-width: 768px) {
    .background {
        display: none;
    }
   
}



@media screen and (max-width: 480px) {
    .heading-container {
        padding-top: 2.5rem;
    }
}


.channel-list {
  overflow-y: scroll;
}

.channel-list::-webkit-scrollbar {
  width: 5px;    
}

.channel-list::-webkit-scrollbar-thumb {
  background-color: gray;
  border-radius: 5px;
}

body {
  overflow-y: scroll;
}

body::-webkit-scrollbar {
  width: 7.5px;  
}

body::-webkit-scrollbar {
  background-color: gray;
  border-radius: 5px;
}

.appbar {
  z-index: 100;
}

.username {
  display: flex;
  text-align: end;
}

.message-area {
  position: fixed;
  bottom: 0;
  width: inherit;
  margin-bottom: 20px;
  z-index: 2;
  background-color: white;
  border-radius: 5px;
  resize: none;
}

.message-area-container {   
 position: fixed;
 bottom: 0;
 height: 100px;
 width: calc(100vw - 750px);
 z-index: 1;
 background-color: white;
 display: flex;
 align-items: center;
}

.greetings {
  position: fixed;
  bottom: 0;
  padding-bottom: 100px;  
  padding-right: 240px;
}

.side-navigation {
  height: calc(100% - 60px);  
  width: 100px;  
  background-color: gray;    
  display: flex;  
  flex-direction: column;
  align-items: center;
  position: fixed;
  justify-content: space-between;  
}

.side-navigation-item {
  display: flex;
  flex-direction: column;
}

.main-sidebar {
  margin-left: 120px;
}

.sidebar-container {
  display: flex;
  justify-content: space-between;
}

.right-side-bar {  
  position: relative;
  z-index: 0;
}

.sign-out-button {
  /* style={{fontSize:"0.75rem", paddingBottom: '10px'}}>  */
  font-size: 0.75rem;
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}

.MuiDialogContent-root.css-ypiqx9-MuiDialogContent-root {
  overflow: unset;
}

.MuiPaper-root.MuiPaper-elevation.MuiPaper-elevation6.MuiSnackbarContent-root.css-1eqdgzv-MuiPaper-root-MuiSnackbarContent-root {
  background-color: green;
}

@media screen and (max-width: 600px) {
  
  .message-area-container { 
  width: 90vw;

  }
}
